/* eslint-disable */
import { version } from '../package.json';
const ErrorTypes = {
    network: {
        code: 1,
        msg: '',
        remark: 'videoxhr'
    },
    mse: {
        code: 2,
        msg: '',
        remark: ''
    },
    parse: {
        code: 3,
        msg: '',
        remark: 'mp4hlsflvjs'
    },
    format: {
        code: 4,
        msg: '',
        remark: ''
    },
    decoder: {
        code: 5,
        msg: '',
        remark: ''
    },
    runtime: {
        code: 6,
        msg: '',
        remark: ''
    },
    timeout: {
        code: 7,
        msg: '',
        remark: ''
    },
    other: {
        code: 8,
        msg: '',
        remark: ''
    }
}

class Errors {
    constructor(type, currentTime, duration, networkState, readyState, src, currentSrc,
        ended, errd = {
            line: '',
            handle: '',
            msg: '',
            version: ''
        }, errorCode, mediaError) {
        let r = {}
        if (arguments.length > 1) {
            r.playerVersion = version // 
            r.errorType = type
            r.domain = document.domain // domain
            r.duration = duration // 
            r.currentTime = currentTime
            r.networkState = networkState
            r.readyState = readyState
            r.currentSrc = currentSrc
            r.src = src
            r.ended = ended
            r.errd = errd // 
            r.ex = (ErrorTypes[type] || {}).msg // 
            r.errorCode = errorCode
            r.mediaError = mediaError
        } else {
            const arg = arguments[0]
            Object.keys(arg).map(key => {
                r[key] = arg[key]
            })
            r.ex = ((arg.type && ErrorTypes[arg.type]) || {}).msg
        }
        return r
    }
}
export default Errors
