/* eslint-disable */
class INDEXDB {
    constructor(mydb = {
        name: 'hkplayer',
        version: 1,
        db: null,
        ojstore: {
            name: 'hk-m4a',
            keypath: 'vid'
        }
    }) {
        this.indexedDB = window.indexedDB || window.webkitindexedDB
        this.IDBKeyRange = window.IDBKeyRange || window.webkitIDBKeyRange // 
        this.myDB = mydb
    }

    openDB(callback) {
        // (ObjectStore)
        let self = this
        let version = this.myDB.version || 1
        let request = self.indexedDB.open(self.myDB.name, version)
        request.onerror = function (e) {
            // console.log('e.currentTarget.error.message')
        }
        request.onsuccess = e => {
            this.myDB.db = e.target.result
            // console.log(':' + this.myDB.name + ' version' + this.myDB.version)
            callback.call(self)
        }
        request.onupgradeneeded = e => {
            let db = e.target.result
            let transaction = e.target.transaction
            let store
            if (!db.objectStoreNames.contains(self.myDB.ojstore.name)) {
                // 
                store = db.createObjectStore(self.myDB.ojstore.name, {
                    keyPath: self.myDB.ojstore.keypath
                })
                // console.log('' + this.myDB.ojstore.name)
            }
        }
    }

    deletedb() {
        // 
        let self = this
        self.indexedDB.deleteDatabase(this.myDB.name)
        // console.log(this.myDB.name + '')
    }

    closeDB() {
        // 
        this.myDB.db.close()
        // console.log('')
    }

    addData(storename, data) {
        // 
        let store = this.myDB.db.transaction(storename, 'readwrite').objectStore(storename)
        let request
        for (let i = 0; i < data.length; i++) {
            request = store.add(data[i])
            request.onerror = function () {
                // console.error('add')
            }
            request.onsuccess = function () {
                // console.log('add')
            }
        }
    }

    putData(storename, data) {
        // 
        let store = this.myDB.db.transaction(storename, 'readwrite').objectStore(storename)
        let request
        for (let i = 0; i < data.length; i++) {
            request = store.put(data[i])
            request.onerror = function () {
                // console.error('put')
            }
            request.onsuccess = function () {
                // console.log('put')
            }
        }
    }

    getDataByKey(storename, key, callback) {
        let self = this
        // 
        let store = this.myDB.db.transaction(storename, 'readwrite').objectStore(storename)
        let request = store.get(key)
        request.onerror = function () {
            // console.error('getDataByKey error')
            callback.call(self, null)
        }
        request.onsuccess = function (e) {
            let result = e.target.result
            // console.log('')
            callback.call(self, result)
        }
    }

    deleteData(storename, key) {
        // 
        let store = this.myDB.db.transaction(storename, 'readwrite').objectStore(storename)
        store.delete(key)
        // console.log('' + storename + '' + key + '')
    }

    clearData(storename) {
        // 
        let store = this.myDB.db.transaction(storename, 'readwrite').objectStore(storename)
        store.clear()
        // console.log('' + storename + '')
    }
}

export default INDEXDB
